@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.active{
  color: #f00;
}

ul li {
  list-style: none;
}
.Nav{
    margin: 0;
    padding: 0;
}
.Nav .navbar{
    background: #a41e1d;
    padding: 0 !important;
}
.Nav .list-item{
    margin: 0;
    padding: 0;
}
.Nav .navbar-list{
    display: inline-block;
    float: left;
    margin: 0 auto;
    border-right: 1px solid #820606;
}

.Nav  .navbar-list:hover{
    background: #000;
}
.Nav  .nav-link{
    color: #ffa7a6;
}

.Nav .nav-link:hover{
    background: #393939;
    color: #fff;
}

.Nav  .fa-sign-out-alt{
    color: #fff;
    padding: 12px;
    background: #393939;
}

.Nav .active {
    background: #393939;
    color: #fff;
}

@media screen and (max-width: 768px){
    .nav-navbar{
        display: none;
    }
    .side-nav{
        display: block;
        position: absolute;
        top: 40px;
        right: 0;
        width: 60%;
        height: 100vh!important;
        background: #a41e1d;
        height: auto;
        z-index: 5;
        padding-top: 30px;
    }
    .list-item{
        text-align: center;
    }
    .Nav .navbar-list{
        display: block!important;
        float: none;
        border-right: none;   
    }
    .menu-bar{
        position: absolute;
        left: auto;
        z-index: 3;
        background: #393939;
        padding: 6px 8px;
    }
    
    .bar1, .bar2, .bar3{
        height : 4px;
        background: #fff;
        width: 25px;
        display:block;
        border-radius : 50px;
        margin: 4px 0;
        -webkit-transition: all 0.2s ease-out;
        transition: all 0.2s ease-out;
    }
    
    .changebar1{
        -webkit-transform : rotate(-45deg) translate(-5px, 6px);
                transform : rotate(-45deg) translate(-5px, 6px);
    }
    
    .changebar2{
        opacity: 0;
    }
    
    .changebar3{
        -webkit-transform : rotate(45deg) translate(-5px,-6px);
                transform : rotate(45deg) translate(-5px,-6px);
    }
    .overlay {
        position: fixed;
        display: none;
        width: 100%;
        height: 100%;
        top: 40px;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.6);
        z-index: 1;
    }
    .show{
        display: block!important;   
    }
}
.header-content{
    height: 100px;
}

.header-content img{
    width: 150px;
}

.header-image{
    margin: auto 25px;
}

.header-content h5{
    color: #393939;
    margin-top: 35px;
    margin-bottom: 0px;

}

.header-content p{
    color: #cb8362;
    font-weight: 600;
}

@media screen and (max-width: 768px){
    .header-content{
        height: 80px;
        line-height: 60px;
    }    
    .header-content img{
        width: 150px;
    }
    .header-content h5{
        display: none;
    }
    .header-content p{
        margin: 10px;
    }
    .header-content p:before {
        content : 'CallNow :';
        color: #000;
    }
}

@media screen and (max-width: 400px){
    .header-content p{
        margin: 0;
        margin-right: 10px;
    }
    .header-content p:before {
        display: none;
    }
    .header-content{
        height: 60px;
    }
    .header-image{
        margin-left: 10px;
    }
}
.Banner {
    background: url(/FlooringStore/static/media/banner.27a60d30.jpg) no-repeat 0 -30px;
    height: 400px;
    background-size: cover;
}

.Banner .nav-bar{
    background: #373735;
}

.Banner .navbar-list-item{
    border-right: 1px solid #555;
}

.Banner .nav-link {
    padding: 12px 20px;
    color: #eee;
}

.Banner .nav-link:hover{
    background: #000;
}

.Banner .banner-marquee{
    margin-top: 315px;
    background: rgba(0, 0,0, 0.8);
}

.Banner .banner-marquee p{
    line-height: 37px;
    color: #fff;
    margin: 0 20px;
}

.Banner .slide-button{
    margin: 10px 0;
}
.Banner .slide-circle{
    display: inline-block;
    background: #aaa;
    height: 15px;
    width: 15px;
    margin: 0 5px;
    border-radius: 50%;
}

.Banner span.slide-active{
    display: block;
    background: #333;
    height: 8px;
    width: 8px;
    border-radius: 50%;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-55%,-55%);
            transform: translate(-55%,-55%);
}


@media screen and (max-width: 768px){
    .Banner {
        background-position: 0 0;
        height: 300px;
    }
    .Banner .banner-nav{
        margin-left: 0 auto;
    }
    .Banner .navbar-list-item{
        margin: 0 auto;
        font-size: 14px;
    }
    .Banner .nav-link {
        padding: 10px 5px;
    }
    .Banner .navbar-list-item{
        margin: auto;
        text-align: center;
        border: none;
    }
    .Banner .banner-marquee{
        margin-top: 230px;
    }
    .Banner .banner-marquee p{
        line-height: 30px;
        color: #fff;
        font-size: 14px;
        text-align: center;
        margin: 0 20px;
        white-space: nowrap;
        overflow-x: scroll;
    }

}

@media screen and (max-width: 500px){
    .Banner {
        background-position: -50px 0;
    }   
    
}
.Section {    
    background: #f5f4f0;
}

.Section .section-content{
    background: #fff;
    height: auto;
    padding: 50px 0;
}
.Section .section-layer{
    padding: 100px 0 50px 0;
    margin: auto 2.5%;
}
.Section .collection-image img{
    width: 300px;
    height: 150px;
    padding: 10px;
    box-shadow: 0 0 10px #555;
}
.Section .collection-content h3{
    color: #73301a;
    margin: 10px 0;
}

.Section .collection-content p:first-of-type{
    color: #9e4426;
}

.Section .btn-custom {
    color: #fff;
    background: #73301a;
}

@media screen and (max-width: 1200px) {
    .Section .collection-image img{
        width: 250px;
    }    
}

@media screen and (max-width: 992px) {
    .Section .collection-image img{
        width: 200px;
        height: 150px;
    }
    .Section .section-layer{
        margin: auto;
    }    
}

@media screen and (max-width: 768px){
    .Section .section-content{
        padding-top: 0;
    }
    .Section .section-layer{
        padding: 75px 0 25px 0;
        margin: 0 5%;
    }    
    .Section .collection-image img{
        width: 80%;
        height: 200px;
        padding: 10px;
        box-shadow: 0 0 10px #555;
    }
    .Section .collection-content{
        margin: 10% 5% ;
    }
    .Section .collection-content:last-of-type{
        margin-bottom: 0;
    }
    .Section .collection-content h3{
        font-size: 24px;
    }
}

@media screen and (max-width: 768px){
    .Section .section-layer{        
        margin: auto;
    }    
    .Section .collection-image img{
        width: 100%;
        height: 200px;
    }
}

@media screen and (max-width: 500px) {
    .Section .section-layer{
        padding: 50px 0 25px 0;
    }
}
